@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.9/css/weather-icons.min.css);

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: #c5eef3;
    height: 100vh;
    min-height: 100vh;
    display: grid;
    place-items: center;
  }

.widget {
    display: flex;
    height: 400px;
    width: 650px;
    flex-wrap: wrap;
    cursor: pointer;
    border-radius: 20px;
    box-shadow: 0 27px 55px 0 rgba(0, 0, 0, 5), 0 17px 17px 0 rgba(0, 0, 0, 5);
    justify-content: center;
  }

.widget .weatherIcon,#wicon {
  flex: 1 100%;
  height: 50%;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  background: #fafafa;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 100px;
}
#wicon{
  height: 250px;
}

.widget .weatherInfo {
  flex: 0 0 70%;
  height: 40%;
  background: #080705;
  display: flex;
  align-items: center;
  color: white;
}

.widget .weatherInfo .temperature {
  flex: 0 0 40%;
  width: 100%;
  font-size: 65px;
  display: flex;
  justify-content: space-around;
  margin-right: 20px;
}

.widget .weatherInfo .description {
  flex: 0 60%;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: center;
}

.widget .weatherInfo .description .weatherCondition {
  text-transform: uppercase;
  font-size: 35px;
  font-weight: 100;
}

.widget .weatherInfo .description .place {
  font-size: 15px;
}

.widget .date {
  flex: 0 0 30%;
  height: 40%;
  background: #70c1b3;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: white;
  font-size: 30px;
  font-weight: 800;
  text-align: center;
}

.widget .weather-stat{
  flex:1 100%;
  padding: 3% 0;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  background: #fafafa;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  font-size: 100px;
}

.widget .weather-stat p, .bold{
  font-size: 25px;
  font-weight: 600;
}

.temp-info-minmax,.weather-info,.two-section{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  text-align: center;
  align-items: center;
  color: #70c1b3;
  background-color: blanchedalmond;
}


.widget .weather-stat .info-leftside {
  text-align: left;
  font-weight: 600;
  text-transform: capitalize;
  color: #080705;
  font-size: 15px;
}

/* input search button style  */

.search {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
}

.searchCity {
  width: 50%;
  border: none;
  padding: 16px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  font-size: 18px;
}

.searchCity:focus {
  color: #00b4cc;
}

.searchBtn {
  width: 80px;
  height: 32px;
  border: none;
  background: #048b9c;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
  outline: none;
  font-size: 16px;
}

.searchBtn:hover {
  background-color: #0ebed5;
}

.wrap {
  display: flex;
  justify-content: center;
  margin-top: -150px;
  margin-bottom: 50px;
}
